import React from 'react';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';

const AboutUs = () => {
  const mapStyles = {        
    height: "400px",
    width: "100%"
  };
  
  const defaultCenter = {
    lat: 17.4483, lng: 78.3915
  };

  return (
    <div style={{ padding: '20px' }}>
      <h1>About Us</h1>
      <p>
        Welcome to Chavan Constructions. We are a leading construction company based in Hyderabad.
        Our mission is to deliver high-quality construction services that meet the unique needs
        of our clients. With years of experience in the industry, we have built a reputation for
        excellence and reliability.
      </p>
      <h2>Our Location</h2>
      <LoadScript
        googleMapsApiKey=''> 
         <GoogleMap
          mapContainerStyle={mapStyles}
          zoom={13}
          center={defaultCenter}
         >
          <Marker position={defaultCenter}/>
         </GoogleMap>
      </LoadScript>
    </div>
  );
}

export default AboutUs;
